import React, { useState, useEffect } from 'react';
import { Card, CardHeader, CardContent, CardActions, Typography, Button, Checkbox, FormControlLabel, FormGroup, TextField, MenuItem, Slider, Box, Grid } from '@mui/material';
import { styled } from '@mui/material/styles';

// 常態分佈的累積分佈函數 (CDF)
const normalCDF = (x, mean, stdDev) => {
  return (1 - Math.exp(-0.5 * Math.pow((x - mean) / stdDev, 2))) / 2;
};

const StyledCard = styled(Card)(({ theme }) => ({
  maxWidth: 800,
  margin: 'auto',
  padding: theme.spacing(3),
  backgroundColor: theme.palette.background.paper,
  boxShadow: theme.shadows[3],
}));

const StyledSlider = styled(Slider)(({ theme }) => ({
  color: theme.palette.primary.main,
  '& .MuiSlider-thumb': {
    height: 24,
    width: 24,
    backgroundColor: '#fff',
    border: '2px solid currentColor',
  },
  '& .MuiSlider-valueLabel': {
    fontSize: 12,
    fontWeight: 'normal',
    top: -6,
    backgroundColor: 'unset',
    color: theme.palette.text.primary,
    '&:before': {
      display: 'none',
    },
    '& *': {
      background: 'transparent',
      color: theme.palette.mode === 'dark' ? '#fff' : '#000',
    },
  },
}));

const DatingFilter = () => {
  const [filters, setFilters] = useState({
    heightRange: [160, 180],
    weightRange: [50, 70],
    genders: [],
    ageRange: [25, 35],
    minIncome: 50000,
    education: '',
    ethnicities: [],
  });
  const [result, setResult] = useState(null);
  const [prScore, setPrScore] = useState(null);

  const [totalUsers, setTotalUsers] = useState(1); // 避免除以零
  const [sumProbabilities, setSumProbabilities] = useState(0);
  const [sumSquaredProbabilities, setSumSquaredProbabilities] = useState(0);
  const [isFirstCalculation, setIsFirstCalculation] = useState(true);

  const handleChange = (name, value) => {
    setFilters(prev => ({ ...prev, [name]: value }));
  };

  const handleCheckboxChange = (name, value, checked) => {
    setFilters(prev => ({
      ...prev,
      [name]: checked 
        ? [...prev[name], value]
        : prev[name].filter(item => item !== value)
    }));
  };

  const calculateProbability = () => {
    let probability = 1;

    // 身高範圍概率
    const heightMean = 170;
    const heightStdDev = 10;
    const heightProb = normalCDF(filters.heightRange[1], heightMean, heightStdDev) - normalCDF(filters.heightRange[0], heightMean, heightStdDev);
    probability *= heightProb;

    // 體重範圍概率
    const weightMean = 70;
    const weightStdDev = 15;
    const weightProb = normalCDF(filters.weightRange[1], weightMean, weightStdDev) - normalCDF(filters.weightRange[0], weightMean, weightStdDev);
    probability *= weightProb;

    // 性別概率
    const genderProb = filters.genders.length > 0 
      ? filters.genders.reduce((acc, gender) => {
          const genderProbabilities = {
            'male': 0.488,
            'female': 0.488,
            'transgender_male': 0.012,
            'transgender_female': 0.012
          };
          return acc + genderProbabilities[gender];
        }, 0)
      : 1; // 如果沒有選擇性別，則概率為 1
    probability *= genderProb;

    // 計算年齡範圍概率
    const ageRange = filters.ageRange[1] - filters.ageRange[0];
    const ageProb = Math.min(1, ageRange / 62);
    probability *= ageProb;

    // 計算收入概率
    const minIncome = Math.max(0, filters.minIncome);
    const incomeProb = Math.max(0.1, 1 - (Math.log(minIncome + 1) - Math.log(30001)) / (Math.log(1000001) - Math.log(30001)));
    probability *= incomeProb;

    // 計算教育程度概率
    const educationProb = {
      'highschool': 0.6,
      'university': 0.25,
      'postgraduate': 0.05
    };
    const eduProb = filters.education ? educationProb[filters.education] : 1;
    probability *= eduProb;

    // 計算種族概率
    const ethnicityProb = filters.ethnicities.length > 0
      ? filters.ethnicities.reduce((acc, ethnicity) => {
          const ethnicityProbabilities = {
            'east_asian': 0.21,
            'south_asian': 0.24,
            'southeast_asian': 0.08,
            'central_asian': 0.01,
            'white': 0.15,
            'black': 0.15,
            'hispanic': 0.09,
            'middle_eastern': 0.07
          };
          return acc + ethnicityProbabilities[ethnicity];
        }, 0)
      : 1; // 如果沒有選擇種族，則概率為 1
    probability *= Math.min(1, ethnicityProb);

    // 更新統計數據
    const newTotalUsers = totalUsers + 1;
    const newSumProbabilities = sumProbabilities + probability;
    const newSumSquaredProbabilities = sumSquaredProbabilities + probability * probability;

    setTotalUsers(newTotalUsers);
    setSumProbabilities(newSumProbabilities);
    setSumSquaredProbabilities(newSumSquaredProbabilities);

    // 計算平均值和標準差
    const mean = newSumProbabilities / newTotalUsers;
    const variance = Math.max(0, (newSumSquaredProbabilities / newTotalUsers) - (mean * mean));
    const stdDev = Math.sqrt(variance);

    // 計算 PR 值
    let prValue;
    if (isFirstCalculation) {
      prValue = Math.min(100, probability * 200); // 調整初始計算方法
      setIsFirstCalculation(false);
    } else {
      const zScore = (probability - mean) / (stdDev || 0.0001);
      prValue = Math.min(100, Math.max(0, (zScore + 4) / 8 * 100)); // 調整 z-score 轉換
    }

    setResult((probability * 100).toFixed(4)); // 增加小數位數以顯示更精確的結果
    setPrScore(prValue.toFixed(2));
  };

   // 使用 useEffect 來在組件加載時初始化統計數據
   useEffect(() => {
    const initialProbability = 0.05; // 調整初始概率
    setTotalUsers(1);
    setSumProbabilities(initialProbability);
    setSumSquaredProbabilities(initialProbability * initialProbability);
  }, []);

  return (
    <StyledCard>
      <CardHeader title="交往配對篩選器" titleTypographyProps={{variant: 'h4', align: 'center'}} />
      <CardContent>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Box mb={2}>
              <Typography gutterBottom>身高範圍 (cm)</Typography>
              <StyledSlider
                value={filters.heightRange}
                onChange={(e, newValue) => handleChange('heightRange', newValue)}
                valueLabelDisplay="auto"
                min={140}
                max={200}
              />
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Box mb={2}>
              <Typography gutterBottom>體重範圍 (kg)</Typography>
              <StyledSlider
                value={filters.weightRange}
                onChange={(e, newValue) => handleChange('weightRange', newValue)}
                valueLabelDisplay="auto"
                min={40}
                max={120}
              />
            </Box>
          </Grid>
          <Grid item xs={12}>
            <Typography gutterBottom>性別（可複選）:</Typography>
            <FormGroup row>
              {['male', 'female', 'transgender_male', 'transgender_female'].map((gender) => (
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={filters.genders.includes(gender)}
                      onChange={(e) => handleCheckboxChange('genders', gender, e.target.checked)}
                      name={gender}
                    />
                  }
                  label={gender === 'male' ? '男性' : gender === 'female' ? '女性' : gender === 'transgender_male' ? '跨性別男性' : '跨性別女性'}
                  key={gender}
                />
              ))}
            </FormGroup>
          </Grid>
          <Grid item xs={12} md={6}>
            <Box mb={2}>
              <Typography gutterBottom>年齡範圍</Typography>
              <StyledSlider
                value={filters.ageRange}
                onChange={(e, newValue) => handleChange('ageRange', newValue)}
                valueLabelDisplay="auto"
                min={18}
                max={80}
              />
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography gutterBottom>最低年收入 (美元): ${filters.minIncome}</Typography>
            <TextField
              label="最低年收入"
              type="number"
              value={filters.minIncome}
              onChange={(e) => handleChange('minIncome', Math.max(0, parseInt(e.target.value, 10)))}
              variant="outlined"
              fullWidth
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography gutterBottom>選擇教育程度:</Typography>
            <TextField
              select
              value={filters.education}
              onChange={(e) => handleChange('education', e.target.value)}
              fullWidth
              variant="outlined"
            >
              <MenuItem value="">選擇教育程度</MenuItem>
              <MenuItem value="highschool">高中及以上</MenuItem>
              <MenuItem value="university">大學及以上</MenuItem>
              <MenuItem value="postgraduate">研究所及以上</MenuItem>
            </TextField>
          </Grid>
          <Grid item xs={12}>
            <Typography gutterBottom>種族（可複選）:</Typography>
            <FormGroup row>
              {['east_asian', 'south_asian', 'southeast_asian', 'central_asian', 'white', 'black', 'hispanic', 'middle_eastern'].map((ethnicity) => (
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={filters.ethnicities.includes(ethnicity)}
                      onChange={(e) => handleCheckboxChange('ethnicities', ethnicity, e.target.checked)}
                      name={ethnicity}
                    />
                  }
                  label={
                    ethnicity === 'east_asian' ? '東亞人' :
                    ethnicity === 'south_asian' ? '南亞人' :
                    ethnicity === 'southeast_asian' ? '東南亞人' :
                    ethnicity === 'central_asian' ? '中亞人' :
                    ethnicity === 'white' ? '白人' :
                    ethnicity === 'black' ? '黑人' :
                    ethnicity === 'hispanic' ? '拉丁美洲裔' : '中東和北非裔'
                  }
                  key={ethnicity}
                />
              ))}
            </FormGroup>
          </Grid>
        </Grid>
      </CardContent>
      <CardActions style={{justifyContent: 'center', marginTop: '20px'}}>
        <Button variant="contained" color="primary" size="large" onClick={calculateProbability}>計算機率</Button>
      </CardActions>
      {result !== null && (
        <CardContent>
          <Box textAlign="center" mt={2}>
            <Typography variant="h5" gutterBottom>分析結果</Typography>
            <Typography variant="h6">你的理想對象在人口中的比例約為: {result}%</Typography>
            <Typography variant="h6">你的擇偶條件PR值: {prScore}</Typography>
            <Typography variant="body1" style={{marginTop: '10px'}}>
              {prScore < 20
                ? "你的條件非常嚴格，找到完全符合條件的對象可能會很有挑戰性。考慮適當放寬一些要求可能會有幫助。"
                : prScore < 40
                ? "你的條件相對嚴格，可能需要多花些時間尋找理想對象。"
                : prScore < 60
                ? "你的條件適中，找到理想對象的難度適中。"
                : prScore < 80
                ? "你的條件相對寬鬆，有很多潛在的理想對象。"
                : "你的條件非常寬鬆，應該很容易找到合適的對象。"}
            </Typography>
          </Box>
        </CardContent>
      )}
    </StyledCard>
  );
};

export default DatingFilter;